.page__body {
  color: #2f4f4f;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding-top: 30px;
  display: grid;
  overflow: visible;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  z-index: 1;
  transform-origin: center;
  width: 200px;
  transition: all 1s;
}

.person__details {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.person__extra {
  opacity: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 100%;
}

.person:hover {
  z-index: 10;
  background-color: #eeeeeecb;
  border-radius: 10px;
  padding: 10px;
  transform: scale(1.3);
}

.person:hover .person__extra {
  opacity: 1;
}

.person:hover .photo__image {
  border-radius: 60px;
}

.photo {
  width: 200px;
  position: relative;
}

.photo__image {
  border-radius: 200px;
  width: 100%;
  transition: all 1s;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 100%;
  padding: 7px 11px 7px 12px;
  font-size: 16px;
  position: absolute;
  top: 85px;
  right: 10px;
}

.rank--10 {
  padding: 7px;
}
